<template>
  <div>
        <!-- 示例一 -->
        <div class="line"></div>
        <div class="demoArea" id="demoArea">
             <span class="btn-submit">选择地区</span>
             <input readonly id="area1">
             <input readonly id="area2">
             <input readonly id="area3">
        </div>
<div class="codeReadonlyModule" style="height:622px">
<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
  &lt;script type="text/javascript"&gt;
    import area from "cy_static/area";
    var demoArea = area({
        elem: "#demoArea",
        maxNum: 4,
        items: [{
            "id": "#area1",
            "value": "450000"
        }, {
            "id": "#area2",
            "value": "450800"
        }, {
            "id": "#area3",
            "value": "450821"
        }],
        ajaxFun: function(params, callback) {
            var url = "http://my.ule.com/uleAddressService/uleAddress/querySonAddress.json?jsonp=?";
            var code = params.index == 0 ? "" : params.value;
            var _data = [];
            jsonp(url, {
                code: code
            }, 5000, function(resp) {
                (resp.data || []).forEach(function(item) {
                    _data.push({
                        value: item[0],
                        text: item[1]
                    });
                });
                callback(_data);
            });

        }
    });
    demoArea.show();
  &lt;/script&gt;
</xmp>
</div>
    
  </div>
</template>

<script>
  import {trim} from "../../../assets/js/codeStyle";
  import area from "cy_static/area";
  import jsonp from "cy_static/jsonp";  
  export default {
    name: 'example2',
    data () {
      return {
        outWin : null,// 窗口
        cmOptions:{
              readOnly:"true",
              mode : "text/html",  // 模式
              indentUnit : 2,  // 缩进单位，默认2
              smartIndent : true,  // 是否智能缩进
              tabSize : 4,  // Tab缩进，默认4
              showCursorWhenSelecting : true

        }
      }
    },
    mounted(){
      this.example1();
        var m = this;
        ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
            var dom, valDom, index = 0, getDom = function() {
                index++;
                dom = m.$refs[element + index];
                valDom = m.$refs[element + "Val" + index];
            };
            getDom();
            while (dom) {
                dom.codemirror.setValue(trim(valDom.innerHTML));
                getDom();
            }
        });
    },
    methods: {
        example1 () {
            var demoArea = area({
                elem: "#demoArea",
                maxNum: 4,
                items: [{
                    "id": "#area1",
                    "value": "450000"
                }, {
                    "id": "#area2",
                    "value": "450800"
                }, {
                    "id": "#area3",
                    "value": "450821"
                }],
                ajaxFun: function(params, callback) {
                    var url = "http://my.ule.com/uleAddressService/uleAddress/querySonAddress.json?jsonp=?";
                    var code = params.index == 0 ? "" : params.value;
                    var _data = [];
                    jsonp(url, {
                        code: code
                    }, 5000, function(resp) {
                        (resp.data || []).forEach(function(item) {
                            _data.push({
                                value: item[0],
                                text: item[1]
                            });
                        });
                        callback(_data);
                    });

                }
            });
            demoArea.show();
         
        }
    }
  }
</script> 
<style>
    .CodeMirror{height: auto;line-height: 17px;}
</style>

  <style scoped>
  .btn-submit{padding: 3px 7px;}
  .cy_win{background-color: #fff;}
  .codeModule {margin: 5px 6px;padding: 5px 8px;}
  .codeModule .codeStyle{font-size:14px;color:#999a9a;line-height:19px}
  .demoArea{overflow:hidden;padding:8px 25px 8px 14px;line-height:1.5;background-color: #fff;}
  .demoArea span{float:left;font-size:15px}
  .demoArea input{float:left;width:65px;text-align:center;margin-right:2px;line-height:1.5}
 
  </style>


